:root {
  --yellow: #f5c32c;
  --orange: #fca61f;
  --black: #242d49;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --purple: rgb(238 210 255);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(250, 163, 22, 0.74);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

* {
  font-family: 'Noto Sans KR', sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
}

.button {
  padding: 11px 26px;
  border: none;
  border-radius: 34px;
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  color: white;
  font-size: 16px;
}

.button:hover {
  /* border: 1px solid var(--orange); */
  background: white;
  color: var(--orange);
  cursor: pointer;
}

.blur {
  position: absolute;
  z-index: -9;
  border-radius: 50%;
  background: #edd0ff;
  width: 22rem;
  height: 14rem;
  filter: blur(72px);
}
